import axios from 'axios';
import $axios from '../plugins/ajax'
import conf from "../plugins/ajax/conf";

let getDoneService={

    //
    getDoneList(data) {
        return $axios.ajax({
            url: '/H_roleplay-si/ds/getMyDoneTask',
            data:data,
        });
    },

}

export default getDoneService;


